.main {
    width: 100%;
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-size: 1.2em;
}

.row {
    display: flex;
    flex-direction: row;
    margin: 0vh 20vw 5vh 20vw;
    width: 60%;
    padding: 10px 0px 10px 0px;
}

.featured {
    font-weight: 800;
    margin: 8vh 20vw 0 20vw;
    padding: 0 0 4vh 0;
}

.projects-card {
    background-color:#FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    width: 50%;
    border-radius: 5px;
    padding: 5vh 5vw 2vh 5vw;
    margin: 0px 5px 0px 5px;
}

.projects-link {
    text-decoration: none;
}

.image {
    width: 100%;
    height: 75%;
    padding: 2vh 2vw 0vh 2vw;
}

.projects-card:hover {
    opacity:0.5;
    transition: all 0.5s ease;
    background-color: #42776a;
}

.projects-card:hover .overlay{
    color: white;
    text-align: left;
    transition: all 0.5s ease;
    font-size: 1.5vw;
}

.overlay {
    color: black;
    text-align: left;
    font-size: 1.5vw;
}